.question_form {
    background-color: #f2f2f2;
    padding-bottom: 30px;
}

.section {
    margin: auto;
    width: 50%;
}

.question_form_top {
    background-color: #ffffff;
    border-top: 8px solid #757575;
    border-radius: 8px;
    padding: 30px 25px;
    text-transform: capitalize;
}

.question_form_top_name,
.question_form_top_desc {
    box-sizing: border-box;
    font-family: 'Google Sans', Roboto, Arial, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid #cccccc;
    background-color: transparent;
    color: #333333;
    height: 35px;
    margin-top: 10px;
}

.question_form_top_desc {
    font-size: 14px;
    height: 20px;
}

.question_boxes {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.add_question {
    background-color: #ffffff;
    border-radius: 8px;
    padding: 25px 22px;
    text-transform: capitalize;
    display: flex;
    flex-direction: column;
    padding-top: 0px;
    width: 93%;
    margin-left: 10px;
}

.add_question_top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.question {
    box-sizing: border-box;
    font-family: 'Google Sans', Roboto, Arial, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    width: 40%;
    border: none;
    outline: none;
    color: #333333;
    height: 40px;
    margin-top: 10px;
    margin-right: 10px;
    padding: 10px;
    background-color: #ffffff;
}

.question:focus {
    border-bottom: 1px solid #757575;
    background-color: #f2f2f2;
}

.select {
    height: 40px;
    width: 230px;
    padding: 5px 15px;
    color: #333333;
    margin-left: 10px;
    margin-right: 10px;
    border: 1.5px solid #cccccc;
    border-radius: 3px;
    background: transparent;
}

.menuitem {
    color: rgba(51, 51, 51, 0.3);
}

.add_question_body {
    display: flex;
    align-items: center;
}

.text_input {
    outline: none;
    border: none;
    height: 40px;
    width: 490px;
    font-family: Roboto, Arial, sans-serif;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: .2px;
    color: #333333;
    background-color: #ffffff;
    padding: 0 10px;
}

.text_input:focus {
    border-radius: 1.5px solid #757575;
}
